@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
:root {
  --margin: 10px;
  --category-margin: 5px;
  --radius: 8px;
  --hadow: 0px 5px 10px -10px black;
}

* {
  margin: 0;
  padding: 0;
}

@media (min-width: 0px) {
  body {
    font-family: 'Roboto', sans-serif;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: #eaeaea;
    transition: color 200ms;
  }

  .header {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 300px;
    width: 100%;
    padding-bottom: 100px;
    color: white;
    background-color: #0A47FF;
    z-index: -1;
  }

  .title {
    display: inline-block;
    font-family: 'Catamaran', sans-serif;
    font-size: 28pt;
    font-weight: 800;
    text-align: left;
    padding: 10px 0px 0px 20px;
  }

  .theme-btn {
    vertical-align: top;
    float: right;
    margin: 18px 15px 0px 0px;
    width: 40px;
    height: 40px;
  }

  .theme-btn.light {
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
  }

  .theme-btn.dark {
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
  }

  .container {
    display: block;
    margin-top: 80px;
    padding: 10px;
    border-radius: 40px;
    background-color: #eaeaea;
  }

  h1 {
    font-weight: 400;
    font-size: 18pt;
    text-align: center;
    padding-bottom: 10px;
    padding-bottom: var(--margin);
  }

  button {
    display: inline-block;
    border: none;
    outline:none;
    border-radius: 100px;
    background-color: #0A47FF;
    box-shadow: var(--shadow);
    color: white;
  }

  input {
    -webkit-appearance: none;
    font-family: 'Roboto', sans-serif;
    border: none;
    outline: none;
    border-radius: 8px;
    border-radius: var(--radius);
    margin-bottom: 5px;
    margin-bottom: var(--category-margin);
    background-color: #eee;
    box-shadow: var(--shadow);
    transition: 0.2s;
  }

  input:hover {
    background-color: #eee;
    box-shadow: 0px 0px 3px 0px #0A47FF;
  }

  input:focus {
    background-color: #eee;
    box-shadow: 0px 0px 3px 0px #0A47FF;
  }

  form {
    -webkit-appearance: none;
  }

  .add-btn {
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    background-color: #0A47FF;
    margin-left: 5px;
    margin-left: var(--category-margin);
    vertical-align: top;
  }

  .remove-btn {
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    background-color: #ff384b;
    margin-left: 5px;
    margin-left: var(--category-margin);
    vertical-align: top;
  }

  .remove-btn.confirm-btn {
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    background-color: #00e271;
  }

  .float-right {
    float: right;
  }

  .mobile-view-only {
    display: none;
  }
}

@media (min-width: 768px) {
  .header {
    display: none;
  }

  .container {
    display: none;
  }

  .mobile-view-only {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: #0A47FF;
    color: white;
  }

  .mobile-view-only h1 {
    font-size: 36pt;
    padding-bottom: 10px;
  }

  .mobile-view-only img {
    display: block;
    width: 200px;
    margin: 20px auto 0px auto;
  }
}
.spending {
    width: 100%;
    height: 80px;
    text-align: center;
    background-color: #00fa7f;
    border: 1px solid #ccc;
    border-radius: var(--radius);
    box-shadow: var(--shadow);
    margin-bottom: var(--margin);
    padding: 13px;
    box-sizing: border-box;
}

.spending h3 {
    font-weight: 400;
    font-size: 12pt;
    margin-top: 2px;
}

.spending p {
    font-weight: 500;
    font-size: 18pt;
}

.income {
    color: green;
}

.expenses {
    color:red;
}
:root {
    --category-font-size: 16pt;
}

@media (min-width: 0px) {

    .category {
        position: relative;
        padding: var(--category-margin);
        border-radius: 8px;
        border: 1px solid #ccc;
        background-color: white;
        margin-bottom: var(--margin);
        overflow: hidden;
    }

    .category.collapsed {
        height: 32px;
        background-color: white;
        box-shadow: var(--shadow);
    }

    .category.expanded {
        height: auto;
    }

    .category-label {
        display: inline-block;
        font-weight: 400;
        font-size: 16pt;
        font-size: var(--category-font-size);
        margin-bottom: var(--category-margin);
        padding: 3px 5px 4px 5px;
    }

    .category-arrow {
        display: inline-block;
        width: 15px;
        height: 15px;
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        -ms-interpolation-mode: nearest-neighbor;
            image-rendering: -moz-crisp-edges;
            image-rendering: pixelated;
        transition: transform 200ms;
    }
    
    .category-arrow.expanded {
      transform: rotate(0deg);
    }
    
    .category-arrow.collapsed {
      transform: rotate(-90deg);
    }

    .category-label-wrapper {
        display: inline-block;
    }

    .category-field {
        width: 140px;
        font-weight: 400;
        font-size: 16pt;
        font-size: var(--category-font-size);
        padding: 3px 5px 4px 5px;
    }

    .edit-btn {
        vertical-align: top;
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        background-color: #0A47FF;
        width: 32px;
        height: 32px;
        font-size: var(--item-font-size);
        float: right;
    }

    .edit-btn.confirm-btn {
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        background-color: #00e271;
    }

    .category-btn {
        font-size: 16pt;
        font-size: var(--category-font-size);
        width: 32px;
        height: 32px;
    }

    .category-total {
        padding: 0px var(--category-margin) 0px var(--category-margin);
    }

    .category-total.collapsed {
        position: absolute;
        top: 11px;
        right: 5px;
    }

    .category-total h3 {
        display: inline-block;
        font-size: var(--item-font-size);
        font-weight: 400;
    }

    .category-total p {
        font-size: var(--item-font-size);
        display: inline-block;
    }

    .category-add-form {
        display: inline-block;
    }

    .category-add-form input {
        margin: 0px;
        padding: var(--category-margin);
        background-color: white;
        border: 1px solid #ccc;
    }

    .category-add-form input:hover {
        background-color: #fff;
        box-shadow: 0px 0px 5px 0px #0A47FF;
    }

    .category-add-form input:focus {
        background-color: #fff;
        box-shadow: 0px 0px 5px 0px #0A47FF;
    }
    .category-add-form button {
        margin-top: 2px;
    }

    .remove-modal-container {
        position: fixed;
        top: 0px;
        left: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 1000;
        animation: fade 200ms;
    }
    
    @keyframes fade {
      from {background-color: rgba(0, 0, 0, 0.0);}
      to {background-color: rgba(0, 0, 0, 0.40);}
    }

    .remove-modal {
        position: relative;
        box-sizing: border-box;
        background-color: white;
        border-radius: 8px;
        width: 275px;
        height: 150px;
        padding: 5px;
        animation: move 200ms;
    }
    
    @keyframes move {
      from {top: -25px; opacity: 0;}
      to {top: 0px; opacity: 1;}
    }

    .remove-modal h3 {
        margin-top: 13px;
        text-align: center;
        font-size: 16pt;
    }

    .remove-modal p {
        margin-top: 10px;
        font-size: 10pt;
        text-align: center;
    }

    .btn-wrapper {
        display: block;
        width: 100px;
        margin: 12px auto 0px auto;
    }

    .btn-wrapper .confirm-btn {
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        background-color: #00e271;
        width: 36px;
        height: 36px;
        margin-right: 20px;
    }

    .btn-wrapper .cancel-btn {
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        background-color: #ff384b;
        width: 36px;
        height: 36px;
    }
}
:root {
    --item-font-size: 12pt;
    --item-padding: 3px 6px 3px 6px;
}

@media (min-width: 0px) {
    .item {
        margin: 0px 0px 5px 20px;
        padding: 3px 6px 3px 6px;
        padding: var(--item-padding);
        background-color: #eee;
        border-radius: var(--radius);
        box-shadow: var(--shadow);
    }

    .item-label {
        display: inline-block;
        font-weight: 400;
        font-size: 12pt;
        font-size: var(--item-font-size);
        vertical-align: top;
    }

    .item-value {
        font-size: 12pt;
        font-size: var(--item-font-size);
        float: right;
    }

    .item-btn {
        font-size: 12pt;
        font-size: var(--item-font-size);
        width: 26px;
        height: 26px;
    }

    /* Item Edit */

    .item-edit p {
        display: inline-block;
        font-size: 12pt;
        font-size: var(--item-font-size);
        padding-right: 2px;
    }

    .item-edit input {
        font-size: 12pt;
        font-size: var(--item-font-size);
        padding: 3px 6px 3px 6px;
        padding: var(--item-padding);
    }

    .item-label-field {
        width: 120px;
        margin-left: 20px;
        font-weight: 400;
    }

    .item-value-field {
        width: 60px;
    }

    .item-remove-wrapper {
        display: inline-block;
        position: relative;
        vertical-align: top;
        margin-right: 5px;
        width: 26px;
        height: 26px;
      }
    
      .item-remove-prompt {
        position: absolute;
        width: 96px;
        top: 0px;
        left: -107px;
        color: white;
        background-color: rgba(0, 0, 0, 0.75);
        padding: 3px 6px 3px 6px;
        padding: var(--item-padding);
        border-radius: var(--radius);
        animation: slide 200ms;
      }
      
      @keyframes slide {
        from { left: -82px; opacity: 0;}
        to { left: -107px; opacity: 1;}
      }
}
